export default {
  name: 'MultiInput',
  props: {
    value: {
      type: Array,
      required: true
    },
    header: {
      type: String,
      required: false,
      default: null
    }
  },
  data () {
    return {
      emails: this.value,
      new_mail: null,
      // eslint-disable-next-line no-useless-escape
      reg: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/
    }
  },
  watch: {
    value: {
      immediate: true,
      handler (val) {
        this.emails = val
      }
    }
  },
  methods: {
    appendMail () {
      if (!this.isEmailValid()) {
        this.$toast.error('Provide Proper Email Address')
        return
      }
      for (let i = 0; i < this.emails.length; i++) {
        if (this.emails[i].email == this.new_mail) {
          this.$toast.error('Email Exists ALready!')
          return
        }
      }
      this.emails.push({ email: this.new_mail })
      this.new_mail = null
    },
    isEmailValid () {
      return (this.new_mail == '') ? false : !!(this.reg.test(this.new_mail))
    },
    splice (email) {
      for (let i = 0; i < this.emails.length; i++) {
        if (this.emails[i].email == email) {
          this.emails.splice(i, 1)
          this.$emit('input', this.emails)
        }
      }
    }
  }

}
